import './index.scss';
import { Icon, List } from 'antd';
import { connect } from 'react-redux';
import { axios } from '@/utils/axios';
const Item = List.Item
@connect(
	(state) => ({
		...state
	}),
	(dispatch) => ({})
)
export default class GoodsDetail extends Component {
	state = {
		goodstype: ''
	};
	getCategory = async (categoryId) => {
		const result = await axios.get('/manage/category/info', {params:{categoryId}});
		if (result.data.status === 0 && result.data.data) {
			const goodstype = result.data.data.name;
			this.setState({ goodstype });
		}
	};

	componentWillMount() {
		const { updateGood } = this.props;
		const categoryId = updateGood.categoryId
		this.getCategory(categoryId);
	}
	render() {
		const { updateGood } = this.props;
		const { goodstype } = this.state;
		const BASE_IMG = 'https://tianjiangyang.top:3000/upload/';
		return (
			<div className="goodsAdmin">
				<div className="content">
					<div className="contentTop">
						<Icon type="arrow-left" style={{ marginLeft: 20 }} onClick={() => this.props.history.go(-1)} />
						<span> 商品详情 </span>
					</div>
					<div className="contentBottom">
						<List>
							<Item>
								<span className="detail-left">商品名称:</span>
								<span>{updateGood.name}</span>
							</Item>
							<Item>
								<span className="detail-left">商品描述:</span>
								<span>{updateGood.desc}</span>
							</Item>
							<Item>
								<span className="detail-left">商品价格:</span>
								<span>{updateGood.price}元</span>
							</Item>
							<Item>
								<span className="detail-left">所属分类:</span>
								<span>{goodstype}</span>
							</Item>
							<Item>
								<span className="detail-left">商品图片:</span>
								<span>
									{updateGood.imgs &&
										updateGood.imgs.map((img) => (
											<img className="detail-img" key={img} src={BASE_IMG + img} alt="img" />
										))}
								</span>
							</Item>
							<Item>
								<span className="detail-left">商品详情:</span>
								<div dangerouslySetInnerHTML={{ __html: updateGood.detail }} />
							</Item>
						</List>
					</div>
				</div>
			</div>
		);
	}
}
